* {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Ubuntu", sans-serif;
}

*::-webkit-scrollbar {
  width: 7px;
  background-color: #7773;
}

*::-webkit-scrollbar-thumb {
  background-color: #222c;
}

*::-webkit-scrollbar-thumb:hover {
  background-color: #222;
}

*::-webkit-scrollbar-track {
  background-color: #f8f9fa;
}

#root {
  background-color: #f8f9fa;
}

.blog-container {
  display: flex;
  padding-top: 5vh;
  min-height: 75vh;
  place-items: center;
  margin-bottom: 90px;
  flex-direction: column-reverse;
}

.blog-container .loader {
  width: 100%;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.blog-container .utilities {
  width: 30%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.blog-container .utilities .account-alert {
  font-size: 1.2rem;
  font-weight: 500;
  margin-left: 2rem;
  text-align: center;
}

.blog-container .utilities .utility-item {
  margin-bottom: 2rem;
  font-size: 1.6rem;
  font-weight: 700;
  cursor: pointer;
}

.blog-container .utilities .like:hover {
  color: #f33838;
}

.blog-container .utilities .liked {
  color: #f33838;
}

.blog-container .utilities .comment:hover {
  color: #368dd6;
}

.blog-container .blog {
  width: 55%;
  display: flex;
  align-self: center;
  justify-content: center;
}

.blog-container .blog .card {
  width: 100%;
  border: none;
  background-color: transparent;
}

.blog-container .blog .card .author-section {
  display: flex;
  place-items: center;
  justify-content: flex-start;
}

.blog-container .blog .card .blog-image {
  max-height: 28rem;
  border-radius: 2px;
}

.blog-container .blog .card .card-body {
  padding: 0.5rem;
}

.blog-container .blog .card h1 {
  font-weight: 700;
  font-size: 2.2rem;
  margin-top: 2.5rem;
  letter-spacing: 0.08rem;
}

.blog-container .blog .card .postHeader {
  display: flex;
  margin-bottom: 2rem;
  place-items: center;
  justify-content: space-between;
}

.blog-container .blog .card .postHeader .share {
  border-radius: 50%;
  padding: 0.4rem 0.6rem;
  transition-duration: 0.5s;
}

.blog-container .blog .card .postHeader .share:hover {
  cursor: pointer;
  background-color: #aaa5;
}

.blog-container .blog .postHeader .dateCategory {
  display: flex;
  place-items: center;
  justify-content: center;
}

.blog-container .blog .postHeader .dateCategory #divider {
  width: 1px;
  height: 20px;
  margin: 0 0.5rem;
  background-color: #3336;
}

.comment-box form button {
  width: 30%;
  color: #fff;
  display: flex;
  cursor: pointer;
  border-radius: 7px;
  align-items: center;
  margin-bottom: 2rem;
  padding: 0.5rem 1rem;
  justify-content: center;
  background-color: #368dd6;
}

.comment-box form button:hover {
  background-color: #257cc5;
}

.comment-container .comment {
  padding: 1rem 1rem;
  border-radius: 7px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.19);
}

.comment-container .comment .comment-author {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.2rem;
}

.comment-container .comment .comment-date {
  font-size: 0.8rem;
  font-weight: 400;
  color: #181717;
  margin-bottom: 0.5rem;
}

.comment-container .comment .comment-content {
  font-size: 1.2rem;
  font-weight: 500;
  color: #181717;
}

.copy-link .copy-link-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.copy-link .copy-link-container .url {
  font-size: 1rem;
  font-weight: 500;
  color: #d64531;
  word-break: break-all;
}

.copy-link .copy-link-container .copy {
  cursor: pointer;
  transition: 0.3s;
}

.copy-link .copy-link-container .copy:hover {
  color: #f33838;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.copy-link .copied-text {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 0.7rem;
  font-weight: 500;
}

.copy-link .copied-text span {
  border-radius: 7px;
  padding: 0.5rem 1rem;
}

@media only screen and (max-width: 1024px) {
  .blog-container .blog {
    width: 70%;
  }
}

@media only screen and (max-width: 426px) {
  .blog-container .blog {
    width: 90%;
  }
}

.author {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-bottom: 1rem;
  place-items: center;
  justify-content: flex-start;
}

.author .author-label {
  color: #333;
  font-weight: 600;
  font-size: 0.8rem;
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-right: 0.3rem;
}

.author .author-name {
  color: #333d;
  font-weight: 500;
  font-size: 0.8rem;
  text-align: center;
  font-style: italic;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

@media only screen and (max-width: 320px) {
  .author .author-label,
  .author .author-name {
    text-align: left;
    margin-top: 0.4rem;
  }
}

.category-badge {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.date {
  color: #bbb;
  font-weight: 400;
  font-size: 0.8rem;
}

.footer {
  left: 0;
  bottom: 0;
  width: 100%;
  color: #fff;
  cursor: default;
  padding: 0.7rem 0;
  font-size: 15px;
  font-size: 0.9rem;
  text-align: center;
  position: relative;
  background-color: #368dd6;
}

.footer .footerLogoLink {
  color: #fff;
  font-size: 0.9rem;
  font-weight: bold;
  text-decoration: none;
}

.footer .footerLogoLink:hover {
  cursor: pointer;
  text-decoration: underline;
}

* {
  font-family: "Ubuntu", sans-serif;
}

.navbar {
  margin-bottom: 1vh;
  background-color: #fff;
  box-shadow: 0 3px 10px #2222;
}

.navbar .container .navbar-brand {
  cursor: pointer;
  color: #368dd6;
  font-style: italic;
  font-weight: bolder;
}

.navbar .container .navbar-collapse {
  width: 100%;
  flex-grow: 0;
  justify-content: space-between;
}

.navbar .container .navbar-collapse .searchBar {
  width: 90%;
  height: 2.3rem;
}

.navbar .container .navbar-collapse .searchBar form {
  width: 100%;
}

.navbar .container .navbar-collapse form input {
  width: 18vw;
  color: #333;
  margin-left: 2vw;
  font-size: 0.9rem;
  border-right: none;
  background-color: #fff;
  border-radius: 20px 0 0 20px;
  border-top: 1px solid #3336;
  border-left: 1px solid #3336;
  border-bottom: 1px solid #3336;
}

.navbar .container .navbar-collapse form input:focus {
  outline: none;
  box-shadow: none;
  border-right: none;
  border-top: 1px solid #368dd6aa;
  border-left: 1px solid #368dd6aa;
  border-bottom: 1px solid #368dd6aa;
}

.navbar .container .navbar-collapse .searchBar form .search-button {
  height: 2.3rem;
  padding: 0.45rem;
  border-radius: 50%;
  border-left: none;
  margin-left: -0.6rem;
  transition-duration: 0.3s;
  background-color: #ffffff;
  border-radius: 0 20px 20px 0;
  border-top: 1px solid #3336;
  border-right: 1px solid #3336;
  border-bottom: 1px solid #3336;
}

.navbar .container .navbar-collapse form input:focus ~ .search-button {
  border-left: none;
  border-top: 1px solid #368dd6aa;
  border-right: 1px solid #368dd6aa;
  border-bottom: 1px solid #368dd6aa;
}

.navbar .container .navbar-collapse .new-post {
  width: 100%;
  border: none;
  font-size: 0.8rem;
  margin-right: 1vw;
  border-radius: 4px;
  background-color: #368dd6;
}

.navbar .container .navbar-collapse a {
  display: flex;
  font-size: 0.8rem;
  place-items: center;
  justify-content: center;
}

.navbar .container .navbar-collapse .new-post:hover {
  background-color: #146bb4;
}

.navbar .container .navbar-collapse .user-icon {
  border-radius: 50%;
  background-color: #2e2d2e;
  border: 1px solid #111111;
  transition: all 0.3s ease-in-out;
}

.navbar .container .navbar-collapse .user-icon:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.navbar .container .navbar-collapse .dropdown-toggle::after {
  display: none;
}

.navbar .container .navbar-collapse .login-button {
  width: 100%;
  border: none;
  color: #fff;
  font-size: 0.8rem;
  margin-right: 1vw;
  border-radius: 4px;
  background-color: #368dd6;
}

.navbar .container .navbar-collapse .login-button:hover {
  background-color: #146bb4;
}

@media only screen and (max-width: 768px) {
  .navbar .container .navbar-collapse .searchBar {
    width: 100%;
    margin-top: 2rem;
  }

  .navbar .container .navbar-collapse .searchBar input {
    margin-left: 0;
  }

  .navbar .container .navbar-collapse .login {
    margin-top: 1rem;
  }
}

.extended-nav {
  width: 100%;
  padding: 1rem;
  z-index: 2022;
  height: 100vh;
  position: fixed;
  padding-top: 0.5rem;
  flex-direction: column;
  background-color: #ffffff;
}

.extended-nav .close-button-container {
  display: flex;
  padding: 0 7.5%;
  place-items: center;
  margin-bottom: 0.5rem;
  justify-content: space-between;
}

.extended-nav .close-button-container .close-button {
  border-radius: 50%;
}

.extended-nav .close-button-container .close-button:hover {
  background-color: #2222;
}

.extended-nav .extended-nav-items {
  display: flex;
  padding: 0 12%;
  flex-wrap: wrap;
  flex-direction: column;
  place-items: flex-start;
  justify-content: stretch;
}
@media only screen and (max-width: 768px) {
  .extended-nav .close-button-container {
    padding: 0 3%;
  }
}

.extended-nav-link {
  color: #333;
  font-weight: 500;
  font-size: 1.4rem;
  text-decoration: none;
  transition-duration: 0.4s;
}

.extended-nav-link:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}

@media only screen and (max-width: 768px) {
  .extended-nav-link {
    font-size: 1rem;
    margin: 0.15rem 0;
  }
}

.category-container {
  min-height: calc(100vh - 34px);
}

.category-container .main-heading {
  margin: 2rem 0;
  font-size: 1.2rem;
  text-align: center;
}

.category-container .loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.category-container .container {
  width: 50%;
  padding-top: 3vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  margin-bottom: 30px;
}

.category-container h1.message {
  font-weight: 400;
