    font-size: 1rem;
  }

  .blog-card .card-body-bottom {
    margin-top: 0.5rem;
  }

  .blog-card .card-body-bottom .created-at {
    flex-direction: column;
    place-items: flex-start;
  }

  .blog-card .card-body-bottom .created-at .divider {
    display: none;
  }

  .blog-card .card-body-bottom .created-at .date {
    font-size: 0.7rem;
    margin-bottom: 0.4rem;
  }
}

