.home-container .container .no-explore,
.home-container .no-post {
  display: none;
}

.home-container .loader {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-container .latest-news {
  width: 79%;
  display: flex;
  flex-wrap: wrap;
  margin-top: 2rem;
  justify-content: space-between;
}

.home-container .latest-news .news-card:nth-of-type(1) {
  flex-grow: 4;
}

.home-container .explore .resetCategory {
  cursor: pointer;
  border-radius: 4px;
  margin-left: 0.5rem;
  padding: 0.3rem 0.8rem;
  border: 1px solid #1111;
  transition-duration: 0.4s;
  background-color: #f5f5f5;
}

.home-container .explore .resetCategory:hover {
  background-color: #d5d5d5;
}

@media only screen and (max-width: 1024px) {
  .home-container {
    flex-direction: column;
  }

  .home-container .container {
    width: 70%;
  }
}

@media only screen and (max-width: 768px) {
  .home-container .latest-news {
    width: 90%;
  }

  .home-container .container {
    width: 100%;
  }

  .home-container .blog-card {
    width: 100%;
  }

  .home-container .container .explore {
    width: 100%;
    flex-direction: column;
    place-items: flex-start;
  }

  .home-container .container .explore h1 {
    margin-right: 0;
    margin-bottom: 1rem;
  }

  .home-container .container .explore div {
    width: 100%;
    justify-content: flex-end;
  }
}

.news-card * {
  cursor: pointer;
}

.news-card {
  width: 35%;
  flex-grow: 1;
  height: 15rem;
  margin: 0.2rem;
  min-width: 18rem;
  border-radius: 4px;
}

.news-card .tint {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 1.3rem;
  border-radius: 4px;
  flex-direction: column;
  background-color: #111a;
  transition-duration: 0.8s;
}

.news-card .tint:hover {
  background-color: #111d;
}

.news-card .tint .category {
  color: #fff;
  font-weight: 600;
  font-size: 0.6rem;
  border-radius: 2px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 0.05rem 0.3rem;
  text-transform: uppercase;
  background-color: #368dd6;
}

.news-card .tint .date {
  color: #ccc;
  font-size: 0.75rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-top: 0.5rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.news-card .tint h3 {
  color: #fff;
  margin-top: 1rem;
  margin-bottom: 0;
  font-size: 1.3rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

@media only screen and (max-width: 426px) {
  .news-card {
    height: 13rem;
  }
}

* {
  font-family: "Ubuntu", sans-serif;
}

.login-container {
  display: flex;
  min-height: 90vh;
  align-items: center;
  justify-content: center;
}

.login-container .card {
  width: 35%;
  border: none;
  border-radius: 10px;
  padding: 1rem 0.5rem;
  background-color: transparent;
}

.login-container .card .card-title {
  font-size: 2rem;
  font-weight: bold;
  color: #343335;
  text-align: left;
  margin-bottom: 2rem;
}

.login-container .card form input {
  width: 100%;
}

.login-container .card form button {
  width: 100%;
  color: #ffffff;
  margin-top: 1rem;
  border-radius: 5px;
  font-size: 1.3rem;
  font-weight: 500;
  cursor: pointer;
  border: #368dd6;
  background-color: #368dd6;
}

.login-container .card .error p {
  color: #c93c23;
  padding-top: 2vh;
  font-size: 1.2rem;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .login-container .card {
    width: 55%;
  }
}

@media only screen and (max-width: 426px) {
  .login-container .card {
    width: 95%;
  }
}

* {
  font-family: "Ubuntu", sans-serif;
}

.not-found {
  width: 100%;
  height: 100vh;
  display: flex;
  place-items: center;
  flex-direction: column;
  justify-content: center;
}

.not-found .illustration {
  width: 25%;
  height: 30%;
}

.not-found .page-not-found {
  font-size: 50px;
  color: #409cb5;
  font-weight: 600;
}

.not-found .back-button {
  color: #3e8ea3;
}

@media only screen and (max-width: 530px) {
  .not-found .illustration {
    width: 90%;
  }
}

@media only screen and (max-width: 500px) {
  .not-found .page-not-found {
    font-size: 32px;
  }
}

* {
  font-family: Ubuntu;
}

.profile-container {
  display: flex;
  padding-top: 3vh;
  min-height: 75vh;
  flex-direction: row;
  margin-bottom: 5rem;
  justify-content: space-around;
}

.profile-container .loader {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.profile-container .profile-card {
  margin-top: 5rem;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.profile-container .card {
  min-width: 22rem;
  border-radius: 6px;
  background-color: #fff;
}

.profile-container .card .card-body .basic-profile {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.profile-container .card .user-icon {
  border-radius: 50%;
  background-color: #222;
}

.profile-container .card h1 {
  font-size: 1.8rem;
  font-weight: 600;
  margin-bottom: 0.1rem;
}

.profile-container .card .user-email {
  color: #333;
  font-weight: 500;
  font-size: 0.8rem;
  margin-bottom: 0.1rem;
}

.profile-container .card .user-info {
  color: #333;
  font-weight: 500;
  font-size: 0.8rem;
}

.profile-container .profile-blogs {
  width: 60%;
}

.profile-container .profile-blogs .main-heading {
  font-weight: 500;
  color: #368dd6;
  font-size: 1.5rem;
  margin-bottom: 2rem;
}

.author {
  margin-bottom: 0;
}

@media only screen and (max-width: 1024px) {
  .profile-container .card {
    min-width: 19rem;
  }
}

@media only screen and (max-width: 768px) {
  .profile-container {
    padding-top: 0;
    place-items: center;
    flex-direction: column;
  }

  .profile-container .card .user-icon {
    margin-right: 1rem;
  }

  .profile-container .profile-blogs .main-heading {
    margin-top: 2rem;
  }

  .profile-container .profile-card {
    width: 90%;
    margin-top: 3rem;
  }

  .profile-container .card .card-body .basic-profile {
    justify-content: flex-start;
  }

  .profile-container .profile-blogs {
    width: 90%;
  }

  .profile-container .profile-blogs .blog-card {
    width: 100%;
  }
}

.search-container {
  min-height: calc(100vh - 34px);
}

.search-container .main-heading {
  margin: 2rem 0;
  font-size: 1.2rem;
  text-align: center;
}

.search-container .loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.search-container .container {
  width: 50%;
  padding-top: 3vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 6px;
  margin-bottom: 30px;
}

.search-container h1.message {
  font-size: 1.3rem;
}

@media only screen and (max-width: 1024px) {
  .search-container {
    flex-direction: column;
  }

  .search-container .container {
    width: 70%;
  }
}

@media only screen and (max-width: 768px) {
  .search-container .container {
    width: 100%;
  }
}

